import __var_0__ from '@stdlib/utils/define-nonenumerable-read-only-property';
import __var_1__ from '@stdlib/utils/copy';
import __var_2__ from '@stdlib/assert/is-windows';
import __var_3__ from '@stdlib/regexp/basename-posix';
import __var_4__ from '@stdlib/regexp/basename-windows';
